/** CARD BASED STYLING */
.page-header-accent {
  padding: 0.75rem 1.5rem 0.5rem;
  border-top: 5px solid #ff9800;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  margin-bottom: 1rem; }

.mat-card-border-primary {
  padding: 0.75rem 1.5rem 0.5rem;
  border-top: 5px solid #3f51b5;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px; }

.mat-icon-button.active {
  background-color: #3f51b5;
  color: #fff; }

.mat-tab-body-content {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto; }

.font-sm {
  font-size: .75rem; }

.font-xs {
  font-size: .5rem; }

vs-daily-sale-tracker > mat-card > mat-card-content > table > tbody > tr > td mat-form-field {
  margin: 0 5px; }

.wp-100 {
  width: 100%; }

body, html {
  height: 100%;
  font-size: 16px;
  line-height: 1.15rem; }

button:focus {
  outline: none !important;
  box-shadow: none !important; }

.form-control:focus {
  outline: none !important;
  border: none !important; }

.app-loading {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%; }

.app-loading .spinner {
  height: 200px;
  width: 200px;
  animation: rotate 2s linear infinite;
  transform-origin: center center;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto; }

.app-loading .spinner .path {
  stroke-dasharray: 1, 200;
  stroke-dashoffset: 0;
  animation: dash 1.5s ease-in-out infinite;
  stroke-linecap: round;
  stroke: #ddd; }

@keyframes rotate {
  100% {
    transform: rotate(360deg); } }

@keyframes dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0; }
  50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35px; }
  100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124px; } }

.hide {
  display: none !important; }

.mat-dialog-container {
  padding: 0.5rem 1.5rem !important; }

.modal-header {
  padding: 0 0 1rem 0;
  border: 0;
  align-items: center; }

.modal-body {
  padding: 0; }

.modal-footer {
  padding: 1rem 0 0; }

.filter-active {
  background-color: #3f51b5 !important;
  color: #fff !important; }

.bg-vs-primary {
  background-color: #3f51b5; }

.bg-vs-muted {
  background-color: #6c757d;
  color: #fff; }

.mat-dialog-container {
  max-height: 80vh !important;
  padding: 1.5rem !important; }

.navigation-accordion mat-expansion-panel {
  -webkit-box-shadow: none !important;
  -moz-box-shadow: none !important;
  box-shadow: none !important; }

.navigation-accordion .mat-expansion-panel-body {
  padding: 0 0 1rem; }

.navigation-accordion .mat-expansion-panel-header {
  padding: 0 1rem 0;
  font-size: 1rem; }
